import React, { Component } from "react";
// import 'abortcontroller-polyfill/dist/abortcontroller-polyfill-only';
import { indexServices } from "../services/index";



class Users extends Component {
  state = {
    githubUsers: '',
  };

  abortController = new AbortController();

  componentWillMount() {
    this.getUser();
  }
  getUser = async () => {
    try{
    const res = await indexServices.getUsers({ signal: this.abortController.signal });
    this.setState({
      githubUsers: res.data.results
    })
    }catch(error) {
      if (error.name === 'AbortError') return; 
      throw error; 
    };
  };
 
  componentWillUnmount() {
    console.log('--abort--')
    this.abortController.abort();
  }

  render() {
    return (
      <>
        <input type="button" value="Show" onClick={this.getUser} />
        {!this.state.githubUsers ? 
        <p>LOading wait...</p>:
        <div>
          {this.state.githubUsers &&
            this.state.githubUsers.map((value, key) => (
              <h2 key={value.id}>name : {value.email}</h2>
            ))}
        </div>
        }
      </>
    );
  }
}
export default Users;
